<template>
  <div class="login-wrap">
    <div class="login-form">
      <div class="form-title"></div>
      <el-form :model="form" :rules="rules" class="form-content" label-width="0px" ref="form">
        <!-- refform -->
        <!-- elemnetUI  -->
        <el-form-item prop="swnumber">
          <el-input :placeholder="form.userType == 1 ? '' : (form.userType == 2 ? '' : '')"
            v-model="form.swnumber">
            <!-- v-model,placeholder"" -->
            <span slot="prepend"><i class="el-icon-user"></i></span>
            <!-- prepend -->
          </el-input>
        </el-form-item>

        <el-form-item prop="password">
          <el-input @keyup.enter.native="submit()" placeholder="" type="password" v-model="form.password" show-password>
            <!-- submit() -->
            <!-- @keyupVue -->
            <span slot="prepend"><i class="el-icon-edit"></i></span>
          </el-input>
        </el-form-item>

        <el-form-item>
          <div style="display: flex">
            <el-input prefix-icon="el-icon-key" v-model="form.validCode" style="width: 50%;"
              placeholder=""></el-input>
            <ValidCode @input="createValidCode" />
          </div>
        </el-form-item>

        <el-form-item prop="userType">
          <el-radio-group v-model="form.userType">
            <el-radio label="1" class="checkbox"></el-radio>
            <el-radio label="2" class="checkbox"></el-radio>
            <el-radio label="3" class="checkbox"></el-radio>
          </el-radio-group>
        </el-form-item>

        <div class="login-btn" v-loading="this.$store.state.loading">
          <!-- Vueloading -->
          <el-button @click="submit()" type="primary"></el-button>
          <!-- @clickVuesubmit() -->
        </div>
      </el-form>
    </div>
  </div>
</template>

<script>
import { login } from "@/api/login";
import ValidCode from "@/components/ValidCode";
export default {
  components: {
    ValidCode
  },
  data: function () {

    return {
      form: {
        swnumber: "",
        password: "",
        userType: "1"
      },
      rules: {
        swnumber: [
          { required: true, message: "", trigger: "blur" }
          // ,
        ],
        password: [{ required: true, message: "", trigger: "blur" }],
        userType: [
          { required: true, message: "", trigger: "blur" }
        ]
      },
      validCode: ""
    };
  },
  methods: {
    //  4
    createValidCode(data) {
      this.validCode = data;
    },
    submit() {
      this.$refs.form.validate(valid => {
        // vaildate
        // Vue.js  validate 
        // validate  true false
        if (valid) {
          if (!this.form.validCode) {
            this.$message.error("");
            return;
          }
          if (this.form.validCode.toLowerCase() !== this.validCode.toLowerCase()) {
            this.$message.error("");
            return;
          }
          login(
            this.form.swnumber,
            this.form.password,
            this.form.userType
          ).then((res) => {
            if (!res.loggedIn) {
              alert("")
            }
            else {
              this.$message.success(": " + res.swnumber);
              this.$store.commit("login", res);
              this.$router.push({ name: "container" });
            }
          });
        }
      });
    }
  }
};
</script>

