<template>
  <div class="info-wrap">
    <div class="crumbs">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item>
          <i class="el-icon-fa fa-id-badge"></i> 
        </el-breadcrumb-item>
      </el-breadcrumb>
    </div>

    <div class="container">
      <el-form :model="entityForm" class="info-form" label-width="80px">
        <el-form-item label="">
          <el-input disabled v-model="entityForm.username"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input disabled v-model="entityForm.name"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input disabled v-model="entityForm.departmentName"></el-input>
        </el-form-item>
        <!-- <el-form-item label="Email">
          <el-input v-model="entityForm.email"></el-input>
        </el-form-item> -->
        <!-- <el-form-item label="">
          <el-date-picker
              format="yyyy-MM-dd"
              placeholder=""
              type="date"
              v-model="entityForm.birthday"
          >
          </el-date-picker>
        </el-form-item> -->
        <el-form-item label="">
          <el-radio-group v-model="entityForm.sex">
            <el-radio :label="1"></el-radio>
            <el-radio :label="0"></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="">
          <el-input type="password" v-model="entityForm.password" show-password></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-button @click="updating = true" type="primary"></el-button>
        </el-form-item>
      </el-form>

      <el-dialog :visible.sync="updating" title="" width="30%">
        <div></div>
        <span class="dialog-footer" slot="footer">
          <el-button @click=update type="primary"> </el-button>
          <el-button @click="updating = false"> </el-button>
        </span> 
      </el-dialog>
    </div>
  </div>
</template>

<script>
import * as api from "../../api/teacher/info";

export default {
  name: "TeacherInfo",
  data() {
    return {
      entityForm: {
        username: "",
        name: "",
        departmentName: "",
        sex: "",
        password: ""
      },
      updating : false
    };
  },
  methods: {
    get() {
      var id = this.$store.state.status.userId
      api.get(id).then(res => {
        this.entityForm = res;
      });
    },
    update() {
      api.update(this.entityForm).then(() => {
        this.$message.success("!");
        this.updating = false
      });
    },

  },
  created() {
    this.get();
  }
};
</script>

<style scoped>
.info-form {
  min-width: 400px;
  width: 35%;
  margin: auto;
}
</style>