<template>
  <div class="student-wrap">
    <div class="crumbs">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item>
          <i class="el-icon-fa fa-user"></i> 
        </el-breadcrumb-item>
      </el-breadcrumb>
    </div>

    <div class="container">
      <div class="query-form">
        <el-row :gutter="20">
          <el-col :span="2">
            <el-button @click="create" icon="el-icon-plus"></el-button>
          </el-col>
          <el-col :offset="13" :span="3">
            <el-input
              @keyup.enter.native="query"
              placeholder=""
              v-model="queryForm.name"
            />
          </el-col>
          <!-- <el-col :span="3">
            <el-input
              @keyup.enter.native="query"
              placeholder=""
              v-model="queryForm.majorName"
            />
          </el-col> -->
          <el-col :span="3">
            <el-input
              @keyup.enter.native="query"
              placeholder=""
              v-model="queryForm.className"
            />
          </el-col>
          <el-col :span="3">
            <el-button @click="query" icon="el-icon-search" type="primary"
              >
            </el-button>
          </el-col>
        </el-row>
      </div>

      <el-row justify="center" type="flex">
        <el-pagination
          :current-page.sync="pageIndex"
          :page-size="pageSize"
          :total="pageSize * pageCount"
          @current-change="getPage"
          background
          layout="prev, pager, next"
        >
        </el-pagination>
      </el-row>

      <div class="table">
        <el-table
            :data="tableData"
            border
            height="390px"
            stripe>
          <el-table-column label="id" prop="id" width="80px" />
          <el-table-column label="" prop="username" />
          <el-table-column label="" prop="name" />
          <el-table-column label="" prop="password" />
          <el-table-column label="" prop="className" />
          <!-- <el-table-column label="" min-width="150px" prop="majorName" /> -->
          <!-- <el-table-column label="" prop="sex" width="80px" /> -->
          <el-table-column align="center" label="" width="200px">
            <template slot-scope="scope">
              <el-button @click="edit(scope.row.id)" size="mini" type="success"
                >
              </el-button>
              <el-button
                @click="startDelete(scope.row.id)"
                size="mini"
                type="danger"
                >
              </el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>

      <el-dialog :visible.sync="editing" title="" width="30%">
        <el-form :model="entityForm" label-width="70px" ref="form">
          <el-form-item label="">
            <el-input v-model="entityForm.username"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="entityForm.name"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="entityForm.className"></el-input>
            <!-- <el-select placeholder="" v-model="entityForm.className">
              <el-option
                :key="index"
                :label="item.name"
                :value="item.id"
                v-for="(item, index) in classes"
              />
            </el-select> -->
          </el-form-item>
          <el-form-item label="">
            <el-input type="password" v-model="entityForm.password" show-password></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input type="password"  v-model="entityForm.confirmPassword" show-password></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-radio-group v-model="entityForm.sex">
              <el-radio :label="1"></el-radio>
              <el-radio :label="0"></el-radio>
            </el-radio-group>
          </el-form-item>
        </el-form>
        <span class="dialog-footer" slot="footer">
          <el-button @click="save" type="primary"> </el-button>
          <el-button @click="editing = false"> </el-button>
        </span>
      </el-dialog>

      <el-dialog :visible.sync="deleting" title="" width="30%">
        <div></div>
        <span class="dialog-footer" slot="footer">
          <el-button @click="deleteItem" type="primary"> </el-button>
          <el-button @click="deleting = false"> </el-button>
        </span> 
      </el-dialog>

    </div>
  </div>
</template>

<script>
import * as api from "../../api/admin/student";
// import * as classApi from "../../api/admin/class";
import {exportInnerMessage}  from "../../common/ajax";

export default {
  name: "AdminStudent",
  data() {
    return {
      queryForm: {
        className: "",
        name: ""
      },
      entityForm: {},
      tableData: [],
      pageSize: api.pageSize,
      pageCount: 1,
      pageIndex: 1,
      editing: false,
      deleting:false,
      deleteId: -1
    };
  },
  methods: {
    query() {
      api
        .getPageCount(
          this.queryForm.className,
          this.queryForm.name
        )
        .then(res => {
          this.pageCount = res.count;
          this.pageIndex = 1;
          this.getPage(1);
        });
    },
    getPage(pageIndex) {
      api
        .getPage(
          pageIndex,this.queryForm.className,this.queryForm.name)
        .then(res => {
          this.tableData = [];
          // for (let i = 0; i < res.length; i++) {
          //   res[i].sex = res[i].sex === 1 ? "" : "";
          // }
          for (var i = 0; i <= Object.keys(res).length; i++) {
            var obj = {
              id: res[i].id,
              username: res[i].username,
              name: res[i].name,
              password: res[i].password,
              className: res[i].className
            };
            this.tableData.push(obj);
          }
          this.tableData = res;
        });
    },
    create() {
      this.entityForm = {
        id: -1,
        username: "",
        name: "",
        className: "",
        password: "",
        confirmPassword:"",
        sex: 0
      };
      this.editing = true;
    },
    edit(id) {
      api.get(id).then(res => {
        this.entityForm = res;
        this.entityForm.confirmPassword = this.entityForm.password
        this.editing = true;
      });
    },
    save() {
      if (this.entityForm.password !=this.entityForm.confirmPassword){
        exportInnerMessage("error","");
      }
      else {
        if(this.entityForm.password === ""){
          this.entityForm.password="A12345678"
        }
        if (this.entityForm.id === -1) {
          api.create(this.entityForm).then(() => {
            this.finishSave();
          });
        } else {
          api.update(this.entityForm).then(() => {
            this.finishSave();
          });
        }
      }
    },
    finishSave() {
      this.$message.success("");
      this.getPage(this.pageIndex);
      this.editing = false;
    },
    startDelete(id){
      this.deleting = true;
      this.deleteId = id;
      // this.deleteItem(id);
    },
    deleteItem() {
      this.deleting = true;
      // alert(this.deleteId)
      api.deleteItem(this.deleteId).then(() => {
        this.$message.success("");
        this.getPage(this.pageIndex);
        this.deleting=false;
      });
    },
    // getClasses() {
    //   classApi.listName().then(res => {
    //     this.classes = res;
    //   });
    // }
  },
  created() {
    this.query();
    // this.getClasses();
  }
};
</script>

<style scoped></style>
