<template>
  <div class="course-select-wrap">
    <div class="crumbs">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item>
          <i class="el-icon-fa fa-book"></i> 
        </el-breadcrumb-item>
      </el-breadcrumb>
    </div>

    <div class="container">
      <div class="query-form">
        <el-row :gutter="20">
          <el-col :offset="15" :span="3">
            <el-input
              @keyup.enter.native="query"
              placeholder=""
              v-model="queryForm.courseName"
            />
          </el-col>
          <el-col :span="3">
            <el-input
              @keyup.enter.native="query"
              placeholder=""
              v-model="queryForm.teacherName"
            />
          </el-col>
          <el-col :span="3">
            <el-button @click="query" icon="el-icon-search" type="primary"
              >
            </el-button>
          </el-col>
        </el-row>
      </div>

      <el-row justify="center" type="flex">
        <el-pagination
          :current-page.sync="pageIndex"
          :page-size="pageSize"
          :total="pageSize * pageCount"
          @current-change="getPage"
          background
          layout="prev, pager, next"
        >
        </el-pagination>
      </el-row>

      <div class="table">
        <el-table
            :data="tableData"
            border
            height="390px"
            stripe>
          <el-table-column label="Id" prop="courseId" />
          <el-table-column label="" prop="courseName" />
          <el-table-column label="" prop="teacherName" />
          <el-table-column label="" prop="time" />
          <el-table-column label="" prop="place" />
          <el-table-column align="center" label="" width="200px">
            <template slot-scope="scope">
              <div v-if="scope.row.haveSelected"> 
                <el-button @click="deleteSelect(scope.row.courseName)" 
                  size="mini" type="danger" 
                  >
                </el-button>
              </div>
              <div v-else>
                <el-button @click="select(scope.row.courseId)" 
                  size="mini" type="success" 
                  >
                </el-button>
              </div>

            </template>
          </el-table-column>
        </el-table>
      </div>
    </div>
  </div>
</template>

<script>
import * as api from "../../api/student/courseSelect";

export default {
  name: "StudentCourseSelect",
  data() {
    return {
      queryForm: {
        courseName: "",
        teacherName: ""
      },
      tableData: [],
      pageSize: api.pageSize,
      pageCount: 1,
      pageIndex: 1
    };
  },
  methods: {
    query() {
      api
        .getPageCount(this.queryForm.courseName, this.queryForm.teacherName)
        .then(res => {
          this.pageCount = res.count;
          this.pageIndex = 1;
          this.getPage(1);
        });
    },
    getPage(pageIndex) {
      api
        .getPage(pageIndex,this.queryForm.courseName,this.queryForm.teacherName,this.$store.state.status.userName)
        .then(res => {
          // this.tableData = res;
          this.tableData = [];
          // alert(Object.keys(res).length)
          // res
          for (var i = 0; i <= Object.keys(res).length; i++) {
            var obj = {
              courseId: res[i].courseId,
              courseName: res[i].courseName,
              teacherName: res[i].courseTeacherName,
              time: ''+res[i].courseDay+''+res[i].courseSession+'',
              place: res[i].coursePlace,
              haveSelected : res[i].haveSelected
            };
            this.tableData.push(obj);
          }
        });
    },
    select(id) {
      var userName = this.$store.state.status.userName
      api.select(id,userName).then(() => {
        this.$message.success("!");
        this.getPage(this.pageIndex);
      });
    },
    deleteSelect(courseName) {
      var userName = this.$store.state.status.userName
      api.deleteSelect(courseName,userName).then(() =>{
        this.$message.success("!");
        this.getPage(this.pageIndex);
      });
    }
  },
  created() {
    this.query();
  }
};
</script>

<style scoped></style>
